.wraper{display:flex;
    height:10vh;
    justify-content:space-between;
    border-bottom: 1px solid blue;
}

.left_side{
    flex:1;
    align-items: center;
    display: flex;
    gap:2rem;
}
.left_name{font-size: 1.3rem;
font-weight: bold;}

.right_side{
    display: flex;
    flex:1;
    align-items: center;
    justify-content: center;
    font-weight: 400;

}



.right_ul{ flex: 10;
}
.right_ul>ul{display: flex;
gap:2rem;
margin-right: 4rem;}

.right_ul>ul:hover{
    cursor:pointer;
   
}
.right_ul>ul>li:hover{
    color: blue;
    

}

.left_name:hover{
    cursor:pointer;
    
    color: blue;
}